export default {
  components: {
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg: 'Hello World!',
    }
  },
  watch:{
    '$route' (to, from) {
      // 对路由变化作出响应...
      //console.log(to);
    }
  },
  computed:{
    indexUrl(){
      return this.$store.state.indexUrl
    }
  }
}



